<template>
  <!--  -->
  <div class="deliverRecoredList_wrapper commonListTab_cnt">
    <!--  -->
    <div v-if="listPageShow && certificationStatus == 3">
      <ul class="common_list">
        <li
          class="drEach_li"
          @click="liClk"
          v-for="(item,index) in optionResume"
          :key="index"
          :id="item.resume.resumeId"
        >
          <div class="left">
            <div class="leftImg">
              <!-- <img class="headPic" src="../../assets/user.png" alt="">
              <img class="genderPic" src="../../assets/man_symbol.png" alt="">-->
              <img
                class="headPic"
                v-if="(item.resume.genderString== '' || item.resume.genderString == '') && (Number(item.resume.bornInteger)<= 24)"
                :src="resumePicOne"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '' || item.resume.genderString == '') && ((Number(item.resume.bornInteger) >= 25 && Number(item.resume.bornInteger) <= 34) || item.resume.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '') && (item.resume.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '' || item.resume.genderString == '') && (Number(item.resume.bornInteger) >= 35 && Number(item.resume.bornInteger) <= 44)"
                :src="resumePicThree"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '' || item.resume.genderString == '') && Number(item.resume.bornInteger) >= 45"
                :src="resumePicFour"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '' && ( Number(item.resume.bornInteger)<= 24)"
                :src="resumePicFive"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '' && ((Number(item.resume.bornInteger) >= 25 && Number(item.resume.bornInteger) <= 34 )|| item.resume.bornInteger == '')"
                :src="resumePicSix"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '' && (Number(item.resume.bornInteger) >= 35 && Number(item.resume.bornInteger) <= 44)"
                :src="resumePicServen"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '' && Number(item.resume.bornInteger) >= 45"
                :src="resumePicEight"
              >

              <img
                v-if="item.resume.genderString == ''"
                class="genderPic"
                src="../../assets/man_symbol.png"
                alt=""
              >
              <img v-else class="genderPic" src="../../assets/woman_symbol.png" alt="">
            </div>
          </div>
          <div class="right">
            <div class="top">
              <p class="lh-1 mb-19">
                <span class="flex_layOut">
                  <span class="name">{{item.resume.resumeName}}</span>
                  <span class="c-354242 f-15 mr-8">{{item.resume.genderString}}</span>
                  <span class="age">{{item.resume.bornInteger}}</span>
                </span>
                <!-- <span class="deliveryState">{{item.resume.source}}</span> -->
                <!--  -->
              </p>
              <p class="text-left mb-21 flex_layout lh-1">
                <img class="deliver_icon" src="../../assets/fly.png" alt="">
                <span class="deliver_txt gray-818D8D flex_layout">
                  <span class="deliverTxt_w"></span>
                  <span class="black-354242 deliver_position ml-10">{{item.demand.jobTypeString}}</span>
                </span>
              </p>
              <p class="text-left flex_layout lh-1">
                <img class="intention_icon" src="../../assets/heart_add.png" alt="">
                <span class="intention_txt gray-818D8D">
                  
                  <span
                    class="black-354242"
                    v-for="(item,index) in item.resume.intentionalCity"
                    :key="index"
                  >{{item.intentionalPlace}}</span>
                </span>
              </p>
            </div>
            <div class="bottom text-left">:{{item.applyTime}}</div>
          </div>
        </li>
      </ul>
      <!-- length10,,length10 -->
      <div class="infinite-status-prompt text-center" v-if="optionResume.length < 10">
        <span></span>
      </div>
      <!--  -->
      <infinite-loading @infinite="deliverRecordListInfiniteFn" v-if="optionResume.length >= 10">
        <!-- length10   v-if="optionResume.length >= 10"-->
        <span class="infinite-load">
          <i class="el-icon-loading"></i>
          <span>~</span>
        </span>
        <span slot="no-more"></span>
      </infinite-loading>
    </div>
    <!--   -->
    <div v-else-if="listPageShow == false" class="blank_page">
      <img src="../../assets/deliver_blank.png" alt="">
      <p>, , ~</p>
    </div>
    <!--  -->
    <div
      v-else-if="certificationStatus == 1 || certificationStatus == 2 || certificationStatus == 4"
      class="auth-no-success"
    >
      <img :src="recruitingListBack" class="bo-data-icon">
      <p class="recruiting-tips">~</p>
    </div>
  </div>
</template>

<script>
import Util from "@/js/mUtils";
import InfiniteLoading from "vue-infinite-loading"; // 
// 
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
import recruitingListBack from "@/assets/post/recruiting_list_back.png"; //icon

export default {
  data() {
    return {
      listPageShow: true, // 
      searchParams: {
        // 
        jobTypeCode1: "",
        jobTypeCode2: "",
        jobTypeCode3: "",
        resumeName: "",
        resumeId: "",
        corpId: ""
      },
      currentPage: 1, //  
      globalApiUrl: domain.testUrl, // 
      qdsGlobalApiUrl: domain.qdsDomain, // 
      applyTime: [], // 
      textCont: [], // , 
      currentTextCont: "", // 
      applyTimeContent: "", // 
      dataListArr: [],
      optionResume: [], //,
      optionDemand: {}, // 
      options: {},
      listId: 0, // 0, 
      totalPage: 0, // 
      // 
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      listPageState: false, //
      certificationStatus: "", //
      recruitingListBack: recruitingListBack
    };
  },
  components: {
    InfiniteLoading
  },
  mounted() {
    this.certificationStatus = sessionStorage.getItem(
      "qdsMCertificationStatus"
    );
    if (this.$route.query.corpId) {
      this.searchParams.corpId = this.$route.query.corpId;
    }
    // 
    if (this.certificationStatus == 3) {
      this.deliverRecoredAllData();
    }
  },
  methods: {
    // 
    deliverRecoredAllData() {
      var that = this;
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      that
        .axios(that.qdsGlobalApiUrl + "channelApply/applyList?token=" + token, {
          method: "get",
          params: {
            userId: userId,
            searchParams: that.searchParams, // 
            page: that.currentPage, // 
            size: 10 // 
          }
        })
        .then(function(response) {
          if (response.data.code == 200) {
            // 200
            var dataArr = response.data.data.applys;
            if (response.data.data.applys.length > 0) {
              that.totalPage = response.data.data.totalPage; // 
              if (that.totalPage > 1) {
                that.listPageState = true;
              } else if (that.totalPage <= 1) {
                that.listPageState = false;
              }
              dataArr.forEach((itemList, index) => {
                if (itemList.resume.intentionalCity) {
                  itemList.resume.intentionalCity.forEach(citys => {
                    // 
                    if (citys.intentionalPlace) {
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null /g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        / null/g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null/g,
                        ""
                      );
                      if (citys.intentionalPlace == " ") {
                        citys.intentionalPlace = citys.intentionalPlace;
                      } else {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                }
                that.applyTime.push(itemList.applyTime); // 
                if (itemList.resume.isSeen == "02") {
                  that.textCont.push("");
                } else if (itemList.resume.isSeen == "01") {
                  that.textCont.push("");
                }

                if (itemList.resume.intentionPosition) {
                  itemList.resume.intentionPosition.forEach(positions => {
                    // 
                    if (positions.positionName) {
                      positions.positionName = positions.positionName.replace(
                        /null /g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        / null/g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        /null/g,
                        ""
                      );
                      if (positions.positionName == " ") {
                        positions.positionName = positions.positionName;
                      } else {
                        positions.positionName = positions.positionName.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                }
                // (1:2:3:4:5:6:7:8)
                if (itemList.demand.recruitDemandType) {
                  if (itemList.demand.recruitDemandType == 1) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 2) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 3) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 4) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 5) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 6) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 7) {
                    itemList.demand.recruitDemandType = "";
                  }
                  if (itemList.demand.recruitDemandType == 8) {
                    itemList.demand.recruitDemandType = "";
                  }
                }
                var objResume = itemList.resume;
                for (var element in objResume) {
                  if (element) {
                    if (
                      objResume[element] == "" ||
                      objResume[element] == null ||
                      objResume[element] == []
                    ) {
                      objResume[element] = "";
                    }
                  } else {
                    console.log("");
                  }
                }
                var objDemand = itemList.demand;
                for (var i in objDemand) {
                  if (i) {
                    if (
                      objDemand[i] == "" ||
                      objDemand[i] == null ||
                      objDemand[i] == []
                    ) {
                      objDemand[i] = "";
                    }
                  } else {
                    console.log("");
                  }
                }
              });
              that.currentTextCont = that.textCont[that.listId]; // textCont   currentTextCont:
              that.applyTimeContent = that.applyTime[that.listId];

              that.dataListArr = dataArr;
              that.options = that.dataListArr[that.listId];
              // that.optionResume = that.dataListArr[that.listId].resume; //
              that.optionResume = that.dataListArr; // optionResume

              that.optionDemand = that.dataListArr[that.listId].demand; // 
              that.listPageShow = true; // listPageShow
            } else if (
              response.data.data.applys.length == 0 &&
              that.currentPage == 1
            ) {
              that.listPageShow = false;
            }
          }
        })
        .catch(function(error) {});
    },
    // 
    liClk(e) {
      this.$router.push("/qdsSearchResume/deliverRecoredDetial"); // 
      var that = this;
      var id = e.currentTarget.getAttribute("id"); // resumeId
      localStorage.setItem("deliverRecordId", id); // resumeId
      var deliverRecordListString = JSON.stringify(that.optionResume); //  JSON
      localStorage.setItem("deliverRecordList", deliverRecordListString); // 
    },
    // 
    deliverRecordListInfiniteFn($state) {
      if (that.listPageState) {
        // listPageStatetrue
        var that = this;
        var token = Util.getCookie("qdsMToken");
        var userId = localStorage.getItem("qdsSCorpUserId");
        that.currentPage = that.currentPage + 1;
        that
          .axios(
            that.qdsGlobalApiUrl + "channelApply/applyList?token=" + token,
            {
              method: "get",
              params: {
                userId: userId,
                searchParams: that.searchParams, // 
                page: that.currentPage, // 
                size: 10 // 
              }
            }
          )
          .then(response => {
            if (response.data.code == 200) {
              if (response.data.data.applys) {
                if (response.data.data.applys.length < 10) {
                  that.listPageState = false;
                } else {
                  that.listPageState = true;
                }

                var drlInfinitedListDataArr = response.data.data.applys;
                drlInfinitedListDataArr.forEach((itemList, index) => {
                  if (itemList.resume.intentionalCity) {
                    itemList.resume.intentionalCity.forEach(citys => {
                      // 
                      if (citys.intentionalPlace) {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null /g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / null/g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null/g,
                          ""
                        );
                        if (citys.intentionalPlace == " ") {
                          citys.intentionalPlace = citys.intentionalPlace;
                        } else {
                          citys.intentionalPlace = citys.intentionalPlace.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  }
                  that.applyTime.push(itemList.applyTime); // 
                  if (itemList.resume.isSeen == "02") {
                    that.textCont.push("");
                  } else if (itemList.resume.isSeen == "01") {
                    that.textCont.push("");
                  }

                  if (itemList.resume.intentionPosition) {
                    itemList.resume.intentionPosition.forEach(positions => {
                      // 
                      if (positions.positionName) {
                        positions.positionName = positions.positionName.replace(
                          /null /g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          / null/g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          /null/g,
                          ""
                        );
                        if (positions.positionName == " ") {
                          positions.positionName = positions.positionName;
                        } else {
                          positions.positionName = positions.positionName.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  }
                  // (1:2:3:4:5:6:7:8)
                  if (itemList.demand.recruitDemandType) {
                    if (itemList.demand.recruitDemandType == 1) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 2) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 3) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 4) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 5) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 6) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 7) {
                      itemList.demand.recruitDemandType = "";
                    }
                    if (itemList.demand.recruitDemandType == 8) {
                      itemList.demand.recruitDemandType = "";
                    }
                  }
                  var objResume = itemList.resume;
                  for (var element in objResume) {
                    if (element) {
                      if (
                        objResume[element] == "" ||
                        objResume[element] == null ||
                        objResume[element] == []
                      ) {
                        objResume[element] = "";
                      }
                    } else {
                      console.log("");
                    }
                  }
                  var objDemand = itemList.demand;
                  for (var i in objDemand) {
                    if (i) {
                      if (
                        objDemand[i] == "" ||
                        objDemand[i] == null ||
                        objDemand[i] == []
                      ) {
                        objDemand[i] = "";
                      }
                    } else {
                      console.log("");
                    }
                  }
                });
                that.optionResume = that.postitionList.concat(
                  drlInfinitedListDataArr
                );
                $state.loaded();
              } else {
                $state.complete();
              }
            } else {
            }
          })
          .catch(error => {});
      } else {
        $state.complete();
      }
    }
  },
  props: []
};
</script>

<style lang="css" src='./style/commonList.css' scoped></style>
<style scoped>
.deliver_position {
  display: inline-block;
  width: 11.625rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.flex_layout {
  display: flex;
  display: -webkit-flex;
  align-items: center;
}

.deliverTxt_w {
  flex-shrink: 0;
}

/*  */
.blank_page {
  width: 100%;
  height: 100%;
  padding-top: 9.71875rem;
  box-sizing: border-box;
  text-align: center;
}

.blank_page > img {
  width: 12.40625rem;
  margin: 0 auto 1.09375rem;
}

.blank_page > p {
  color: #354242;
  font-size: 0.875rem;
  width: 12.6875rem;
  text-align: center;
  margin: 0 auto;
  line-height: 1.375rem;
}

/*  */
.flex_layOut {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: space-between;
}

.c-354242 {
  color: #354242;
}

.f-15 {
  font-size: 0.9375rem;
}

.mr-8 {
  margin-right: 0.5rem;
}

.ml-5 {
  margin-left: 0.3125rem;
}

.ml-10 {
  margin-left: 0.625rem;
}

.mb-19 {
  margin-bottom: 0.59375rem !important;
}

.mb-21 {
  margin-bottom: 0.65625rem !important;
}

.text-center {
  text-align: center;
}
.auth-no-success {
  padding-top: 9.5rem;
  margin: auto 0;
  text-align: center;
}
.auth-no-success img {
  display: inline-block;
  width: 12.15625rem;
  height: 6.96875rem;
}
.auth-no-success p {
  padding-top: 1.9375rem;
  font-size: 14px;
  color: #354242;
}
.lh-1 {
  line-height: 1;
}

.dspFlex {
  display: flex;
  display: -webkit-flex;
}
</style>



