<template>
  <!--  -->
  <div class="searchResume_wrapper" ref="searchResume_wrapper">
    <!-- tab -->
    <mt-navbar v-model="selected" fixed>
      <mt-tab-item id="1">
        <span @click="myResumeTabClk"></span>
      </mt-tab-item>
      <mt-tab-item id="2">
        <span @click="deliverRecoredTabClk"></span>
      </mt-tab-item>
      <mt-tab-item id="3">
        <span @click="telentsRearchTabClk"></span>
      </mt-tab-item>
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <!--  -->
      <mt-tab-container-item id="1">
        <myResumeList-mdl></myResumeList-mdl>        
      </mt-tab-container-item>
      <!--  -->
      <mt-tab-container-item id="2">
        <deliverRecoredList-mdl></deliverRecoredList-mdl>
      </mt-tab-container-item>
      <!--  -->
      <mt-tab-container-item id="3">
        <telentSearchList-mdl></telentSearchList-mdl>
      </mt-tab-container-item>
    </mt-tab-container>
    <!-- tab -->
    <nav-tab :fatherActive="active"></nav-tab>
  </div>
</template>
<script>
import Vue from "vue";
import navTab from "@/components/layout/tab.vue"; // tab
import { Navbar, TabItem } from "mint-ui"; // mint-ui tab
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
import myResumeList from "@/views/resume/myResumeList"; // 
import InfiniteLoading from "vue-infinite-loading"; // 
import deliverRecoredList from "@/views/resume/deliverRecoredList"; // 
import telentSearchList from "@/views/resume/telentSearchList"; // 

export default {
  data() {
    return {      
      selected: "3", // tab-container 
      active: "0" // tab 
    };
  },
  mounted() {
    let dliverRcdDtialIndex = this.$route.query.id; // ,2
    if (this.$route.query.selected) {
      this.selected = this.$route.query.selected;
    }
    if (dliverRcdDtialIndex == 2) {
      this.selected = "2"; // 
    } else if (dliverRcdDtialIndex == 3) {
      this.selected = "3"; // 
    }else if(dliverRcdDtialIndex == 1){
       this.selected = "1"; // 
    }
  },
  components: {
    navTab, // tab
    "myResumeList-mdl": myResumeList, // 
    InfiniteLoading, // 
    "deliverRecoredList-mdl": deliverRecoredList, // 
    "telentSearchList-mdl": telentSearchList // 
  },
  methods: {
    // tab
    myResumeTabClk() {
      this.$refs.searchResume_wrapper.style.paddingTop = 2.4375 + "rem"; // padding-top
    },
    // tab
    deliverRecoredTabClk() {
      this.$refs.searchResume_wrapper.style.paddingTop = 2.4375 + "rem"; // padding-top
    },
    // tab
    telentsRearchTabClk() {
      // this.$refs.searchResume_wrapper.style.paddingTop = 0;
    }
  }
};
</script>

<style lang="css" src='./style/commonList.css' scoped></style>
<style scoped>
.searchResume_wrapper {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
  padding-top: 2.4375rem;
  box-sizing: border-box;
}
</style>





