<template>
  <!--  -->
  <div class="telentSearch_wrapper">
    <!--  -->
    <div class="telentsSearch_selectBar">
      <div class="selectBar_wrapper">
        <div class="joinIndustry_select select_box">
          <p @click="joinIndustryClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 1}"></span>
            <img
              :src="tabActiveIndex == 1 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt=""
            >
          </p>
        </div>
        <div class="wantCity_select select_box">
          <p @click="wantCityClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 2}"></span>
            <img
              :src="tabActiveIndex == 2 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt=""
            >
          </p>
        </div>
        <div class="moreChoice_select select_box">
          <p @click="moreChoiceClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 3}"></span>
            <img
              :src="tabActiveIndex == 3 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt=""
            >
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div
      v-if="allIsResume"
      class="mt-73 telentsSerach_list"
      id="commonListTab_cnt"
      ref="commonListTab_cnt"
      :class="{'mt-52' : dataIdCltMt == 2}"
    >
      <ul class="common_list">
        <li
          class="drEach_li"
          @click="listClkFn"
          v-for="(item, index) in queryResumeList"
          :key="index"
          :id="item.id"
          style="margin-top:.9375rem"
        >
          <div class="left">
            <div class="leftImg">
              <!-- <img class="headPic" src="../../assets/user.png" alt="">
              <img class="genderPic" src="../../assets/man_symbol.png" alt="">-->
              <img
                class="headPic"
                v-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger)<= 24)"
                :src="resumePicOne"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34) || item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == undefined || item.genderString == null || item.genderString == '') && (item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicThree"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && Number(item.bornInteger) >= 45"
                :src="resumePicFour"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && ( Number(item.bornInteger)<= 24)"
                :src="resumePicFive"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34 )|| item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicSix"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicServen"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && Number(item.bornInteger) >= 45"
                :src="resumePicEight"
              >
              <img
                v-if="item.genderString == ''"
                class="genderPic"
                src="../../assets/man_symbol.png"
                alt=""
              >
              <img v-else class="genderPic" src="../../assets/woman_symbol.png" alt="">
            </div>
          </div>
          <div class="right">
            <div class="top newPadding">
              <p class="lh-1 mb-19">
                <span class="flex_layout">
                  <span class="name">{{item.name}}</span>
                  <span class="age" v-if="item.bornInteger != undefined">{{item.bornInteger}}</span>
                  <span v-else class="age"></span>
                  <span class="c-354242 f-30 ml-5" v-if="item.education != ''">{{item.education}}</span>
                  <span v-else class="c-354242 f-30 ml-5"></span>
                </span>
              </p>
              <p class="text-left lh-1 dspFlex mb-20">
                <img class="deliver_icon" src="../../assets/manMin_pic.png" alt="">
                <span class="deliver_txt gray-818D8D">
                  
                  <span
                    class="black-354242"
                    v-if="item.intentionPosition != null && item.intentionPosition != undefined && item.intentionPosition.length == 0"
                  ></span>
                  <span
                    class="black-354242"
                    v-else
                    v-for="(item, index) in item.intentionPosition"
                    :key="index"
                  >{{item.positionName}}</span>
                </span>
              </p>
              <p class="text-left lh-1 dspFlex">
                <img class="intention_icon" src="../../assets/heart_add.png" alt="">
                <span class="intention_txt gray-818D8D">
                  
                  <span
                    v-if="item.intentionalCity != null && item.intentionalCity != undefined && item.intentionalCity.length == 0"
                    class="black-354242"
                  ></span>
                  <!-- <span v-else class="black-354242" v-for="(item, index) in item.intentionalCity" :key="index">{{item.intentionalPlace}}</span>                   -->
                  <span
                    v-else-if="item.intentionalCity.length > 1 && item.intentionalCity[0].intentionalPlace != ''"
                    class="black-354242"
                  >{{item.intentionalCity[0].intentionalPlace}}...</span>
                  <span
                    v-else-if="item.intentionalCity.length > 1 && item.intentionalCity[0].intentionalPlace == ''"
                    class="black-354242"
                  >{{item.intentionalCity[1].intentionalPlace}}...</span>
                  <span v-else class="black-354242">{{item.intentionalCity[0].intentionalPlace}}</span>
                </span>
              </p>
            </div>
          </div>
          <div class="getTel_btn">
            <p class="txt_wrapper">
              <span
                :id="gernerateId(item.id)"
                @click.stop="getTelInfo(item.id)"
                v-if="item.mobile == 'NA'"
              ></span>
              <span v-else>{{item.mobile}}</span>
            </p>
          </div>
        </li>
      </ul>
      <!-- length10,,length10 -->
      <div class="infinite-status-prompt text-center" v-if="queryResumeList.length < 10">
        <span></span>
      </div>
      <!--  -->
      <infinite-loading @infinite="telentSearchListInfiniteFn" v-if="queryResumeList.length > 10">
        <span class="infinite-load">
          <i class="el-icon-loading"></i>
          <span>~</span>
        </span>
        <span class="infinite-load" slot="no-more"></span>
      </infinite-loading>
    </div>
    <div v-else class="blank_page"></div>
    <!-- modal -->
    <slideModal-mdl
      ref="slideModalMdl"
      v-show="slideModalShow"
      @slideModalMaskFn="listenTabActiveIndexFn"
    ></slideModal-mdl>
    <!-- modal -->
    <slideModalCity-mdl
      ref="slideModalCityMdl"
      v-show="slideModalCityShow"
      @slideModalMaskFn="listenTabActiveIndexFn"
    ></slideModalCity-mdl>
    <!-- popup -->
    <mt-popup v-model="popupVisible" position="right" :modal="false">
      <div class="rightPop_box">
        <!--  -->
        <div class="agePop_box">
          <p @click="ageBarClk">
            <span class="f-28 c-0B231F"></span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="">
            </span>
          </p>
          <div class="age_selectBox flexLayout" v-show="ageCntShow">
            <p>
              <input
                type="tel"
                placeholder=""
                v-model="minAgeVal"
                class="text-center ageIpt"
                maxlength="2"
                @input="ageIptChgeFn"
              >
            </p>
            <div></div>
            <p>
              <input
                type="tel"
                :placeholder="maxAgePhlTxt"
                v-model="maxAgeVal"
                class="text-center ageIpt"
                maxlength="2"
              >
            </p>
          </div>
          <ul class="flexLayout mb-100" v-show="ageCntShow">
            <li
              :class="{li_active: ageLiActive == index}"
              v-for="(item,index) in ageArr"
              :key="index"
              @click="ageLiClk(index)"
              class="mb-100"
            >{{item}}</li>
          </ul>
        </div>
        <!--  -->
        <div class="genderPop_box">
          <p @click="genderBarClk">
            <span class="f-28 c-0B231F"></span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="">
            </span>
          </p>
          <ul class="flexLayout mb-100" v-show="genderCntShow">
            <li
              :class="{li_active: genderLiActive == index}"
              v-for="(item,index) in genderArr"
              :key="index"
              @click="genderLiClk(index)"
            >{{item}}</li>
          </ul>
        </div>
        <!--  -->
        <div class="genderPop_box">
          <p @click="degreeBarClk">
            <span class="f-28 c-0B231F"></span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="">
            </span>
          </p>
          <ul class="flexLayout mb-100 degree_ul" v-show="degreeCntShow">
            <li
              :class="{li_active: degreeLiActive == index}"
              v-for="(item,index) in degreeArr"
              :key="index"
              @click="degreeLiClk(index)"
            >{{item}}</li>
          </ul>
        </div>
        <!--  -->
        <div class="footerBtn">
          <button @click="resetBtnClk"></button>
          <button @click="ensureBtnClk"></button>
        </div>
      </div>
    </mt-popup>
    <!-- popupmask -->
    <div class="mask" v-show="maskShow" @click="maskPopUpClk"></div>
    <!-- modal -->
    <div class="score_modal" v-show="jMarkModalShow">
      <!-- <p class="f-28 c-12221F mb-2 lh-1 mb-10">,50,</p>
      <p class="f-28 c-12221F lh-1">?</p> -->
      <mt-checklist v-model="value" :options="options" @change="checkon"></mt-checklist>
      <!-- <div class="modal_footerBtn">
        <div class="btn" @click="jModalCancelBtnClk"></div>
        <div class="btn" @click="jModalSureBtnClk"></div>
      </div> -->
    </div>
    <!-- modal -->
    <div class="score_modal" v-show="showIntegration">
      <p class="f-28 c-12221F mb-2 lh-1 mb-10" style="margin-top:1.9375rem">,,</p>
      <div class="modal_footerBtn">
        <div class="btn" @click="falseIntegration"></div>
        <div class="btn" @click="trueIntegration"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Util from "@/js/mUtils";
import slideModal from "@/views/resume/slideModal"; // modal modal
import slideModalCity from "@/views/resume/slideModalCity"; // modal-modal
import { Popup } from "mint-ui"; // mint-ui pop
Vue.component(Popup.name, Popup);
import { Checklist } from "mint-ui"; // mint-ui CheckList
Vue.component(Checklist.name, Checklist);
import InfiniteLoading from "vue-infinite-loading"; // 
// 
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
import { Notify } from "vant"; // toast
import { Indicator } from 'mint-ui'; // mint-ui 

export default {
  data() {
    return {
      globalApiUrl: domain.testUrl, // 
      qdsGlobalApiUrl: domain.qdsDomain, // 
      searchGlobalApiUrl: domain.searchTestUrl, // 
      slideModalShow: false, // modal
      slideModalCityShow: false, // 
      tabActiveIndex: "", // select
      selectArrowArr: [
        // select
        {
          normol: require("../../assets/below_arrow.png"), // 
          select: require("../../assets/activeBelow_arrow.png") // 
        }
      ],
      popupVisible: false, // pop
      ageArr: [
        "16",
        "16-25",
        "26-35",
        "36-45",
        "46-55",
        "55"
      ], // pop
      ageLiActive: 0, // age16
      minAgeVal: "16", // 
      maxAgeVal: "", // 
      maxAgePhlTxt: "", // placeholder
      genderArr: ["", "", ""], // pop
      genderLiActive: 0, // gender
      degreeArr: [
        // 
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ], // pop
      degreeLiActive: 0, // degree
      maskShow: false, // popupmask
      value: [""], // mint-ui checkList  ,
      options: [
        {
          label: "",
          value: "A"
        }
      ],
      // 
      pageNum: 1, // 
      positionCode: "", // 
      intentionalPlace: "", // 
      education: "", // 
      genderString: "", // 
      bornIntegerMin: "", // 
      bornIntegerMax: "", // 
      allIsResume: true, // 
      idArr: [], // id
      indexArr: [], // 
      queryResumeList: [], // ()
      // 
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      rightPopUpAgeLiClkFlag: "", // li
      minAgeIptChangeFlag: "", // input
      ageCntShow: true, // popup
      genderCntShow: false, // popup
      degreeCntShow: false, // popup
      certificationStatus: "", // 
      jMarkModalShow: false, // modal
      showIntegration: false, //
      resumeId: "",
      resumeIndex: "",
      dataIdCltMt: "", // commonListTab_cntmargin-top
      getUserStorageId:'',  // storageID
      changeFlag: true, // modal
    };
  },
  components: {
    "slideModal-mdl": slideModal, // modal
    "slideModalCity-mdl": slideModalCity, // modal
    InfiniteLoading, // 
    Notify // toast
  },
  mounted() {
    this.getTelentSearchListData(); // 
    this.$refs.commonListTab_cnt.classList.add("mt-52");   
  },
  methods: {    
    // change checkBoxmodal
    checkon(){ 
      this.changeFlag = !this.changeFlag; // 
      if(this.changeFlag == false){  // 
          this.value = ["A"];        
      }
      
      // // A,AID,IDgetTelInfoID                   
      // if(this.value[0] == 'A'){
      //   // 
      //   localStorage.setItem("userCheckBoxShwOrNt", this.getUserStorageId); // userIdstorage,modal
      // }else{
      //   // 
      //   localStorage.removeItem("userCheckBoxShwOrNt", this.getUserStorageId); // storage
      // }
    },
    // -
    joinIndustryClk() {
      this.tabActiveIndex = 1; // select
      this.slideModalShow = true; // modal
      this.$refs.slideModalMdl.slideUpModalShow = true; // modal
      this.$refs.slideModalMdl.maskShow = true; // modal
      // sildeModalheight,maxHeight
      this.$nextTick(function() {
        let leftListHeight = this.$refs.slideModalMdl.$el.children[0]
          .children[1].offsetHeight; // slideModalheight
        let rightListDom = this.$refs.slideModalMdl.$el.children[0].children[1]
          .children[1]; // slideModalDOM
        rightListDom.style.maxHeight = leftListHeight + "px";
        // let rightListDom1 = this.$refs.slideModalMdl.$el.children[0].children[1]
        //   .children[2]; // slideModalDOM
        // rightListDom1.style.maxHeight = leftListHeight + "px";
      });
    },
    // -
    wantCityClk() {
      this.tabActiveIndex = 2; // select
      this.slideModalCityShow = true; // modal
      this.$refs.slideModalCityMdl.slideUpModalShow = true; // modal
      this.$refs.slideModalCityMdl.maskShow = true; // modal
      // sildeModalheight,maxHeight
      this.$nextTick(function() {
        let leftListHeight = this.$refs.slideModalCityMdl.$el.children[0]
          .children[1].offsetHeight; // slideModalheight
        let rightListDom = this.$refs.slideModalCityMdl.$el.children[0]
          .children[1].children[1]; // slideModalDOM
        rightListDom.style.maxHeight = leftListHeight + "px";
        // let rightListDom1 = this.$refs.slideModalCityMdl.$el.children[0]
        //   .children[1].children[2]; // slideModalDOM
        // rightListDom1.style.maxHeight = leftListHeight + "px";
      });
    },
    // -
    moreChoiceClk() {
      this.tabActiveIndex = 3; // select
      this.popupVisible = true; // pop
      this.maskShow = true; // popup-mask
    },
    // popup-mask
    maskPopUpClk() {
      this.popupVisible = false; // pop
      this.maskShow = false; // popup-mask
      this.tabActiveIndex = 0; // select
      this.jMarkModalShow = false; // modal
      this.showIntegration = false; // modal
      this.value = [""];  // mask
    },
    // popUpinput
    ageIptChgeFn() {
      this.ageLiActive = 99; // 16
      if (this.minAgeVal == "") {
        // 16

        //  this.ageLiActive = 0;  // 16
        this.bornIntegerMin = 16; // bornIntegerMin=16
      } else {
        // 
        this.minAgeIptChangeFlag = 1; // 
      }
    },
    // pop li
    ageLiClk(index) {
      this.ageLiActive = index;
      if (index == 0) {
        // 16
        this.minAgeVal = 16; // minAgeValv-model 16
        this.maxAgeVal = ""; //  maxAgeVal v-modelplaceholder
        this.maxAgePhlTxt = ""; // maxAgePhlTxtplaceholder
        this.bornIntegerMin = 16; //   16
        this.rightPopUpAgeLiClkFlag = 0; // li
        this.bornIntegerMax = ""; //  
      } else if (index == 1) {
        // 16-25
        this.minAgeVal = 16;
        this.maxAgeVal = 25;
        this.bornIntegerMin = 16;
        this.rightPopUpAgeLiClkFlag = 1; // li
        this.bornIntegerMax = 25;
      } else if (index == 2) {
        // 26-35'
        this.minAgeVal = 26;
        this.maxAgeVal = 35;
        this.bornIntegerMin = 26;
        this.rightPopUpAgeLiClkFlag = 2; // li
        this.bornIntegerMax = 35;
      } else if (index == 3) {
        // 36-45
        this.minAgeVal = 36;
        this.maxAgeVal = 45;
        this.bornIntegerMin = 36;
        this.rightPopUpAgeLiClkFlag = 3; // li
        this.bornIntegerMax = 45;
      } else if (index == 4) {
        // '46-55'
        this.minAgeVal = 46;
        this.maxAgeVal = 55;
        this.bornIntegerMin = 46;
        this.rightPopUpAgeLiClkFlag = 4; // li
        this.bornIntegerMax = 55;
      } else if (index == 5) {
        // 55
        this.minAgeVal = 55;
        this.maxAgeVal = "";
        this.maxAgePhlTxt = "";
        this.bornIntegerMin = 55;
        this.rightPopUpAgeLiClkFlag = 5; // li
        this.bornIntegerMax = "";
      }
    },
    // pop li
    genderLiClk(index) {
      this.genderLiActive = index;
      if (index == 0) {
        // 0-
        this.genderString = ""; // genderString
      } else if (index == 1) {
        // 1-
        this.genderString = "";
      } else if (index == 2) {
        // 2-
        this.genderString = "";
      }
    },
    // pop li
    degreeLiClk(index) {
      this.degreeLiActive = index;
      if (index == 0) {
        // ""
        this.education = 0; // education
      } else if (index == 1) {
        // 
        this.education = 2;
      } else if (index == 2) {
        // 
        this.education = 3;
      } else if (index == 3) {
        // 
        this.education = 4;
      } else if (index == 4) {
        // 
        this.education = 5;
      } else if (index == 5) {
        // 
        this.education = 6;
      } else if (index == 6) {
        // 
        this.education = 7;
      } else if (index == 7) {
        // 
        this.education = 8;
      } else if (index == 8) {
        // 
        this.education = 9;
      }
    },
    // slideModal,modal
    listenTabActiveIndexFn(data) {
      if (data == 1) {
        this.tabActiveIndex = 0; // select
      } else if (data.Id == 1) {
        // 
        this.dataIdCltMt = 2; // commonListTab_cntmargin-top
        this.tabActiveIndex = 0; // select

        var that = this;
        that.positionCode = data.pstVal; // code;
        var token = Util.getCookie("qdsToken");
        var cuserId = localStorage.getItem("qdsSCorpUserId");
        that
          .axios(
            that.searchGlobalApiUrl +
              "JobInfoSearchController/boolQuearyResume2?token=" +
              token,
            {
              method: "post",
              params: {
                pageNum: that.pageNum, //  
                cuserId: cuserId, // id
                positionCode: that.positionCode, // 
                intentionalPlace: that.intentionalPlace, // 
                education: that.education, // 
                genderString: that.genderString, // 
                bornIntegerMin: that.bornIntegerMin, //
                bornIntegerMax: that.bornIntegerMax //  
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              if (response.data.data.dataList.length) {
                that.allIsResume = true; // ,
                that.queryResumeList = []; // ()
                var dataArr = response.data.data.dataList;
                dataArr.forEach((item, index) => {
                  that.userName = item.name;
                  if (item.intentionalCity) {
                    item.intentionalCity.forEach(citys => {
                      // 
                      if (citys.intentionalPlace) {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null /g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / null/g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null/g,
                          ""
                        );
                        if (citys.intentionalPlace == " ") {
                          citys.intentionalPlace = citys.intentionalPlace;
                        } else {
                          citys.intentionalPlace = citys.intentionalPlace.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  } else {
                    item.intentionalCity = [];
                  }
                  if (item.intentionPosition) {
                    item.intentionPosition.forEach(positions => {
                      // 
                      if (positions.positionName) {
                        positions.positionName = positions.positionName.replace(
                          /null /g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          / null/g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          /null/g,
                          ""
                        );
                        if (positions.positionName == " ") {
                          positions.positionName = positions.positionName;
                        } else {
                          positions.positionName = positions.positionName.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  } else {
                    item.intentionPosition = [];
                  }
                  that.idArr.push(item.id);
                });
                that.queryResumeList = that.queryResumeList.concat(dataArr); // 
              } else {
                that.$message({
                  message: "",
                  type: "warning",
                  duration: 1500
                });
                that.allIsResume = false; // 
              }
            } else {
              // 200
              that.allIsResume = false; // 
            }
          })
          .catch(function() {});
      } else if (data.Id == 2) {
        // 
        this.dataIdCltMt = 2; // commonListTab_cntmargin-top
        this.tabActiveIndex = 0; // select
        var that = this;
