    // 
    getTelInfo(resumeId) {
      this.resumeId = resumeId;
      this.certificationStatus = sessionStorage.getItem("qdsMCertificationStatus");   
      this.getUserStorageId = localStorage.getItem("qdsSCorpUserId"); // ID          

      //  1  2  3  4
      if (this.certificationStatus == 1) {
        // 
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 2) {
        //  - OK
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 3) {
        //  (,modal)         

        // userCheckBoxShwOrNt(ID)ID,-modal
        var cutDownJMarkFlag = localStorage.getItem('userCheckBoxShwOrNt'); // ID                
        if(cutDownJMarkFlag == this.getUserStorageId){  // this.getUserStorageId ID          
          this.jMarkModalShow = false; // modal
          this.maskShow = false; // mask
          this.jModalSureBtnClk(); // modal,modal
        }else{          
          this.jMarkModalShow = true; // modal
          this.maskShow = true; // mask
        }
        
      } else if (this.certificationStatus == 4) {
        //  
        this.$router.push("/qdsAttestation");
      }
    },
    // modal
    jModalCancelBtnClk() {
      this.jMarkModalShow = false; // modal
      this.maskShow = false; // mask
      this.value = [""];  // modal,
    },
    // modal
    jModalSureBtnClk() {
      // loading
        Indicator.open({
          text: '...',
          spinnerType: 'snake'
        });

        this.jMarkModalShow = false; // modal
        this.maskShow = false; // mask

      
      //  modal
      if(this.value[0] == 'A'){
        // 
        localStorage.setItem("userCheckBoxShwOrNt", this.getUserStorageId); // userIdstorage,modal
      }


      //
      var that = this;
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      that.idArr.forEach((item, index) => {
        // id
        if (that.resumeId == item) {
          that.resumeIndex = index;
        }
      });
      that
        .axios(
          that.qdsGlobalApiUrl + "channelResume/followResume?token=" + token,
          {
            method: "post",
            params: {
              userId: userId,
              resumeId: that.resumeId,
              source: "search"
            },
            timeout: 10000
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             Indicator.close(); // loading
            Notify({
              message: "",
              duration: 1500,
              background: "#20D3B3"
            });
            that.queryResumeList[that.resumeIndex].mobile =
              response.data.data.mobile;
            that.queryResumeList[that.resumeIndex].name =
              response.data.data.name;

            that.jMarkModalShow = false; // modal
            that.maskShow = false; // mask
          } else if (response.data.code == 30002) {
             Indicator.close(); // loading
            Notify({
              message: "",
              duration: 1500,
              background: "#FF6262"
            });
            that.jMarkModalShow = false; // modal
            that.showIntegration = true; // modal
            that.maskShow = true; // maks
          } else {
             Indicator.close(); // loading
            Notify({
              message: response.data.message,
              duration: 1500,
              background: "#FF6262"
            });
            that.jMarkModalShow = false; // modal
            that.maskShow = false; // mask
          }
        })
        .catch(function(error) {
           Indicator.close(); // loading
           Notify({
              message: '',
              duration: 1500,
              background: '#FF6262'
          });
          this.value = [""];  // modal,

          // that.jMarkModalShow = false; // modal
          // that.maskShow = false; // mask
        });
    },
    falseIntegration() {
      //  
      this.showIntegration = false;
      this.maskShow = false;
    },
    trueIntegration() {
      //  
      this.showIntegration = false;
      this.maskShow = false;
      this.$router.push("/qdsPersonal"); // 
    }
