<template>
  <!--  -->
  <div class="commonListTab_cnt">
    <!--  -->
    <div v-if="listPageShow && certificationStatus == 3">
      <ul class="common_list">
        <li
          class="drEach_li"
          @click="listClkFn"
          v-for="(item,index) in queryResumeList"
          :key="index"
          :id="item.id"
        >
          <div class="left">
            <div class="leftImg">
              <!-- <img class="headPic" src="../../assets/user.png" alt="">
              <img class="genderPic" src="../../assets/man_symbol.png" alt="">-->
              <img
                class="headPic"
                v-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger)<= 24)"
                :src="resumePicOne"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34) || item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == undefined || item.genderString == null || item.genderString == '') && (item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicThree"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '' || item.genderString == undefined || item.genderString == null || item.genderString == '') && Number(item.bornInteger) >= 45"
                :src="resumePicFour"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && ( Number(item.bornInteger)<= 24)"
                :src="resumePicFive"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34 )|| item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicSix"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicServen"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '' && Number(item.bornInteger) >= 45"
                :src="resumePicEight"
              >
              <img
                v-show="item.genderString == ''"
                class="genderPic"
                src="../../assets/man_symbol.png"
                alt=""
              >
              <img
                v-show="item.genderString == ''"
                class="genderPic"
                src="../../assets/woman_symbol.png"
                alt=""
              >
            </div>
          </div>
          <div class="right">
            <div class="top">
              <p class="lh-1">
                <span class="flex_layOut">
                  <span class="name" v-if="(item.name != null)">{{item.name}}</span>
                  <span class="name" v-else></span>
                  <span class="c-354242 f-15 mr-8">{{item.genderString}}</span>
                  <span class="age" v-if="(item.bornInteger != null)">{{item.bornInteger}}</span>
                </span>
                <span class="deliveryState">{{item.tag}}</span>
              </p>
              <p class="text-left lh-1 dspFlex mb-10">
                <img class="deliver_icon" src="../../assets/fly.png" alt="">
                <span class="deliver_txt gray-818D8D">
                  
                  <span class="black-354242" v-if="(item.intentionPosition.length == 0 )"></span>
                  <span
                    v-else
                    class="black-354242"
                    v-for="(itemPosition, indexPosition) in item.intentionPosition"
                    :key="indexPosition"
                  >{{itemPosition.positionName}}</span>
                </span>
              </p>
              <p class="text-left lh-1 dspFlex">
                <img class="intention_icon" src="../../assets/heart_add.png" alt="">
                <span class="intention_txt gray-818D8D">
                  
                  <span class="black-354242" v-if="(item.intentionalCity.length == 0 )"></span>
                  <!-- <span
                    class="black-354242"
                    v-else
                    v-for="(itemCity, indexCity) in item.intentionalCity"
                    :key="indexCity"
                  >{{itemCity.intentionalPlace}}</span>-->
                  <span
                    class="black-354242"
                    v-else-if="item.intentionalCity.length > 0"
                  >{{item.intentionalCity[0].intentionalPlace}}...</span>
                  <span class="black-354242" v-else>{{item.intentionalCity[0].intentionalPlace}}</span>
                </span>
              </p>
            </div>
            <div class="bottom text-left">:{{item.putTime}}</div>
          </div>
        </li>
      </ul>
      <!--  -->
      <infinite-loading @infinite="myResumeListInfiniteFn" v-if="queryResumeList.length >= 10">
        <span class="infinite-load">
          <i class="el-icon-loading"></i>
          <span>~</span>
        </span>
        <span slot="no-more"></span>
      </infinite-loading>
    </div>
    <!--   -->
    <div v-else-if="listPageShow == false" class="blank_page">
      <img src="../../assets/deliver_blank.png" alt="">
      <p></p>
      <p>~</p>
    </div>
    <!--  -->
    <div
      v-else-if="certificationStatus == 1 || certificationStatus == 2 || certificationStatus == 4"
      class="auth-no-success"
    >
      <img :src="recruitingListBack">
      <p class="recruiting-tips">~</p>
    </div>
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading"; // 
import Util from "@/js/mUtils";
// 
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
// import { Indicator } from 'mint-ui'; // mint-ui 
import recruitingListBack from "@/assets/post/recruiting_list_back.png"; //icon

export default {
  data() {
    return {
      globalApiUrl: domain.testUrl, // 
      qdsGlobalApiUrl: domain.qdsDomain, // 
      listPageShow: true, // 
      queryResumeList: [], // 
      formInline: {
        pageNum: 0 // 
      },
      // 
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      certificationStatus: "", //
      recruitingListBack: recruitingListBack
    };
  },
  components: {
    InfiniteLoading
  },
  mounted() {
    // 
    this.certificationStatus = sessionStorage.getItem(
      "qdsMCertificationStatus"
    );
    if (this.certificationStatus == 3) {
      this.getMyResumeAllData();
    }
  },
  methods: {
    // 
    getMyResumeAllData() {
      // loading
      //    Indicator.open({
      //     text: '...',
      //     spinnerType: 'snake'
      //   });
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      var that = this;
      that
        .axios(
          that.qdsGlobalApiUrl +
            "channelResume/myResumeRepository?token=" +
            token,
          {
            method: "get",
            params: {
              userId: userId,
              lastId: that.formInline.pageNum, // 0
              size: 10
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 50001) {
            that.listPageShow = false; // 
          } else if (response.data.code == 200) {
            if (
              response.data.data.resumes &&
              response.data.data.resumes.length > 0
            ) {
              //   Indicator.close(); // loading
              that.listPageShow = true; // 
              that.formInline.pageNum =
                response.data.data.resumes[
                  response.data.data.resumes.length - 1
                ].id;

              var dataArr = response.data.data.resumes;
              dataArr.forEach((item, index) => {
                // 
                if (item.tag == "001") {
                  item.tag = "";
                } else if (item.tag == "002") {
                  item.tag = "";
                } else if (item.tag == "003") {
                  item.tag = "";
                }
                // 
                if (item.intentionalCity) {
                  item.intentionalCity.forEach(citys => {
                    // 
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      / null/g,
                      ""
                    );
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      /null /g,
                      ""
                    );
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      /null/g,
                      ""
                    );
                    if (citys.intentionalPlace == " ") {
                      citys.intentionalPlace = citys.intentionalPlace;
                    } else {
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        / /g,
                        "/"
                      );
                    }
                  });
                } else {
                  item.intentionalCity = [];
                }
                // 
                if (item.intentionPosition) {
                  item.intentionPosition.forEach(positions => {
                    // 
                    if (positions.positionName) {
                      positions.positionName = positions.positionName.replace(
                        /null /g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        / null/g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        /null/g,
                        ""
                      );
                      if (positions.positionName == " ") {
                        positions.positionName = positions.positionName;
                      } else {
                        positions.positionName = positions.positionName.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                } else {
                  item.intentionPosition = [];
                }
              });
              that.queryResumeList = that.queryResumeList.concat(dataArr);
            } else {
              that.listPageShow = false; // 
            }
          }
        })
        .catch(function(error) {});
    },

    // 
    listClkFn(e) {
      var id = e.currentTarget.getAttribute("id"); // liid
      var that = this;
      var queryResumeListString = JSON.stringify(that.queryResumeList); //  JSON
      localStorage.setItem("qdsResumeList", queryResumeListString); // 
      localStorage.setItem("qdsResumeId", id); // id

      this.$router.push({
        path: "/qdsSearchResume/myResumeDetial",
        query: {
          id: 1
        }
      });
    },
    // 
    myResumeListInfiniteFn($state) {
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      var that = this;
      that
        .axios(
          that.qdsGlobalApiUrl +
            "channelResume/myResumeRepository?token=" +
            token,
          {
            method: "get",
            params: {
              userId: userId,
              lastId: that.formInline.pageNum,
              size: 10
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            if (
              response.data.data.resumes &&
              response.data.data.resumes.length > 0
            ) {
              that.formInline.pageNum =
                response.data.data.resumes[
                  response.data.data.resumes.length - 1
                ].id;
              var dataArr = response.data.data.resumes;
              dataArr.forEach((item, index) => {
                if (item.tag == "001") {
                  item.tag = "";
                } else if (item.tag == "002") {
                  item.tag = "";
                } else if (item.tag == "003") {
                  item.tag = "";
                }
                if (item.intentionalCity) {
                  item.intentionalCity.forEach(citys => {
                    // 
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      / null/g,
                      ""
                    );
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      /null /g,
                      ""
                    );
                    citys.intentionalPlace = citys.intentionalPlace.replace(
                      /null/g,
                      ""
                    );
                    if (citys.intentionalPlace == " ") {
                      citys.intentionalPlace = citys.intentionalPlace;
                    } else {
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        / /g,
                        "/"
                      );
                    }
                  });
                } else {
                  item.intentionalCity = [];
                }
              });
              that.queryResumeList = that.queryResumeList.concat(dataArr);
              $state.loaded();
            } else if (response.data.data.resumes.length == 0) {
              $state.complete();
            } else {
              $state.complete();
            }
          }
        })
        .catch(function(error) {});
    }
  }
};
</script>

<style lang="css" src='./style/commonList.css' scoped></style>
<style scoped>
/*  */
.blank_page {
  width: 100%;
  height: 100%;
  padding-top: 9.71875rem;
  box-sizing: border-box;
  text-align: center;
}

.blank_page > img {
  width: 12.40625rem;
  margin: 0 auto 1.09375rem;
}

.blank_page > p {
  color: #354242;
  font-size: 0.875rem;
  width: 12.6875rem;
  text-align: center;
  margin: 0 auto;
  line-height: 1.375rem;
}

/*  */
.flex_layOut {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: space-between;
}

.c-354242 {
  color: #354242;
}

.f-15 {
  font-size: 0.9375rem;
}

.mr-8 {
  margin-right: 0.5rem;
}

.lh-1 {
  line-height: 1;
}

.dspFlex {
  display: flex;
  display: -webkit-flex;
}

.mb-10 {
  margin-bottom: 0.625rem;
}
.auth-no-success {
  padding-top: 9.5rem;
  margin: auto 0;
  text-align: center;
}
.auth-no-success img {
  display: inline-block;
  width: 12.15625rem;
  height: 6.96875rem;
}
.auth-no-success p {
  padding-top: 1.9375rem;
  font-size: 14px;
  color: #354242;
}
</style>



